import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import '../styles/carousel.css';

import React, { useRef, useState } from 'react';

import Slider from 'react-slick';
import award from '../resorces/images/award.svg';
import img1 from '../resorces/images/slide-7.jpg';
import img2 from '../resorces/images/slide-1.jpg';
import img3 from '../resorces/images/slide-2.jpg';
import img4 from '../resorces/images/slide-3.jpg';
import img5 from '../resorces/images/slide-4.jpg';
import img6 from '../resorces/images/slide-5.jpg';

const PrevArrow = ({ onClick }) => (
  <div className="prev-arrow font-bold" onClick={onClick}>
    &lt;
  </div>
);

const NextArrow = ({ onClick }) => (
  <div className="next-arrow font-bold" onClick={onClick}>
    &gt;
  </div>
);

const MainCarousel = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [isAuto, setIsAuto] = useState(true);

  let sliderRef = useRef(null);
  const play = () => {
    sliderRef.slickPlay();
  };
  const pause = () => {
    sliderRef.slickPause();
  };

  const [settings, setSettings] = useState({
    dots: false,
    infinite: true,
    speed: 600,
    waitForAnimate: false,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: isAuto,
    autoplaySpeed: 10000,
    draggable: false,
    arrows: true,
    fade: true,
    prevArrow: <PrevArrow />,
    nextArrow: <NextArrow />,
    beforeChange: (oldIndex, newIndex) => setCurrentSlide(newIndex),
  });

  const toggleAutoPlay = () => {
    if (!isAuto) play();
    else pause();
    setIsAuto(!isAuto);
  };

  return (
    <div className="w-[100%] slider-container relative">
      <div className="absolute top-0 left-[442px] w-[170px] h-[450px] bg-[rgba(0,0,0,0.3)] z-10">
        <ul className="w-[170px] h-[31px] text-[#fff] font-bold text-sm leading-8">
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer"></li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer">/</li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer"></li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer"></li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer"></li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer">/</li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer"></li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer"></li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer"></li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer"></li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer">/</li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer">/</li>
          <li className="px-[23px] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer">/</li>
          <li className="flex flex-row gap-1 w-[170px] px-[23px] bg-[rgba(0,0,0,0.4)] hover:bg-[rgba(0,0,0,0.8)] cursor-pointer">
            AWARDS
            <img src={award} />
          </li>
        </ul>
      </div>
      <Slider ref={(slider) => (sliderRef = slider)} {...settings}>
        <div>
          <div className="relative">
            <div
              className="h-[450px]"
              style={{
                backgroundImage: `url(${img1})`,
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat',
              }}
            ></div>
            <div className="absolute top-[52px] left-[34%]">
              <p className="h-[40px] text-lg font-bold">
                
                <br />
              </p>
              <h1 className="text-5xl text-[#1f1f1f] mt-[30px] mb-[10px] w-[340px]">
                
                <br />
                 
                <br /> 
              </h1>
              <h2 className="text-lg text-[#666] font-bold"> ,  </h2>
            </div>
          </div>
        </div>
        <div>
          <div className="relative">
            <div
              className="h-[450px]"
              style={{
                backgroundImage: `url(${img2})`,
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat',
              }}
            ></div>
            <div className="absolute top-[52px] left-[34%]">
              <p className="h-[40px] text-lg font-bold">
                
                <br />
              </p>
              <h1 className="text-5xl text-[#1f1f1f] mt-[30px] mb-[10px] w-[340px]">
                
                <br />
                 
                <br />
                 
              </h1>
              <h2 className="text-lg text-[#666] font-bold">  </h2>
            </div>
          </div>
        </div>
        <div>
          <div className="relative">
            <div
              className="h-[450px]"
              style={{
                backgroundImage: `url(${img3})`,
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat',
              }}
            ></div>
            <div className="absolute top-[52px] left-[34%]">
              <p className="h-[40px] text-lg font-bold">
                
                <br />
              </p>
              <h1 className="text-5xl text-[#1f1f1f] mt-[30px] mb-[10px] w-[340px]">
                 
                <br />
                 
                <br />
                 
              </h1>
              <h2 className="text-lg text-[#666] font-bold">  </h2>
            </div>
          </div>
        </div>
        <div>
          <div className="relative">
            <div
              className="h-[450px]"
              style={{
                backgroundImage: `url(${img4})`,
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat',
              }}
            ></div>
            <div className="absolute top-[52px] left-[34%]">
              <p className="h-[40px] text-lg font-bold">
                grn+ 
                <br />
              </p>
              <h1 className="text-5xl text-[#1f1f1f] mt-[30px] mb-[10px] w-[340px]">
                
                <br />
                 
                <br />
                
              </h1>
              <h2 className="text-lg text-[#666] font-bold">  59%</h2>
            </div>
          </div>
        </div>
        <div>
          <div className="relative">
            <div
              className="h-[450px]"
              style={{
                backgroundImage: `url(${img5})`,
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat',
              }}
            ></div>
            <div className="absolute top-[52px] left-[34%]">
              <p className="h-[40px] text-lg font-bold">
                PICK
                <br />
              </p>
              <h1 className="text-5xl text-[#1f1f1f] mt-[30px] mb-[10px] w-[340px]">
                
                <br />
                PICK
                <br />  
              </h1>
              <h2 className="text-lg text-[#666] font-bold">- </h2>
            </div>
          </div>
        </div>
        <div>
          <div className="relative">
            <div
              className="h-[450px]"
              style={{
                backgroundImage: `url(${img6})`,
                backgroundPosition: 'center',
                backgroundSize: 'cover',
                backgroundRepeat: 'no-repeat',
              }}
            ></div>
            <div className="absolute top-[52px] left-[34%]">
              <p className="h-[40px] text-lg font-bold">
                
                <br />
              </p>
              <h1 className="text-5xl text-[#1f1f1f] mt-[30px] mb-[10px] w-[340px]">
                 
                <br />
                
                <br />
                 
              </h1>
              <h2 className="text-lg text-[#666] font-bold">   </h2>
            </div>
          </div>
        </div>
      </Slider>
      <div className="absolute bottom-[9.5%] left-[37%] text-sm text-[#666]">{currentSlide + 1}/6</div>
      <div
        className={`absolute w-[40px] h-[40px] left-[42%] bottom-[7.5%] ${isAuto ? 'auto-active' : 'auto-inactive'}`}
        onClick={toggleAutoPlay}
      ></div>
    </div>
  );
};

export default MainCarousel;
